html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
    border: 0;
    font-family: inherit;
    font-size: 100%;
    font-style: inherit;
    font-weight: inherit;
    margin: 0;
    outline: 0;
    padding: 0;
    vertical-align: baseline;
}

html {
    -webkit-text-size-adjust: none;
}

:focus {
    outline: 0;
}

::selection {
    background: #63c6ae;
    color: #fff;
}

::-moz-selection {
    background: #63c6ae;
    color: #fff;
}

ol,
ul {
    list-style: none;
    margin: 0;
}

table,
th,
td {
    border: 1px solid rgba(0, 0, 0, 0.1);
}

table {
    border-collapse: separate;
    border-spacing: 0;
    border-width: 1px 0 0 1px;
    margin-bottom: 20px;
    width: 100%;
}

caption,
th,
td {
    font-weight: normal;
    text-align: left;
}

th {
    border-width: 0 1px 1px 0;
    padding: 8px;
    text-transform: uppercase;
    color: #333;
    font-size: 17px;
    line-height: 24px;
}

td {
    padding: 8px;
    border-width: 0 1px 1px 0;
}

a img {
    border: 0 none;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
    display: block;
}

embed,
iframe,
object {
    max-width: 100%;
}

/* =Global
----------------------------------------------- */
body,
input,
textarea {
    color: #777;
    font: 13px 'PT Sans', sans-serif;
    line-height: 20px;
    word-wrap: break-word;
}

body {
    background-color: #ececec;
}

.wrapper {
    width: 100%;
    background-color: #fff;
}

.container {
    margin: 0 auto;
    max-width: 978px;
}

/* Headings */
h1,
h2,
h3,
h4,
h5,
h6 {
    color: #333;
}

h1 {
    font-size: 28px;
    line-height: 35px;
}

h2 {
    font-size: 26px;
    line-height: 33px;
}

h3 {
    font-size: 24px;
    line-height: 31px;
}

h4 {
    font-size: 22px;
    line-height: 29px;
}

h5 {
    font-size: 20px;
    line-height: 27px;
}

h6 {
    font-size: 17px;
    line-height: 24px;
    text-transform: uppercase;
}

/* No Border/Margin/Padding */
.no-margin-top {
    margin-top: 0px !important;
}

.no-margin-bottom {
    margin-bottom: 0px !important;
}

.no-margin-left {
    margin-left: 0px !important;
}

.no-margin-right {
    margin-right: 0px !important;
}

.no-padding-top {
    padding-top: 0px !important;
}

.no-padding-bottom {
    padding-bottom: 0px !important;
}

.no-padding-left {
    padding-left: 0px !important;
}

.no-padding-right {
    padding-right: 0px !important;
}

.no-border-top {
    border-top: 0 none !important;
}

.no-border-bottom {
    border-bottom: 0 none !important;
}

.no-border-left {
    border-left: 0 none !important;
}

.no-border-right {
    border-right: 0 none !important;
}

/* Horizontal Line */
hr {
    border-color: rgba(0, 0, 0, 0.1);
    border-style: solid none none;
    border-width: 1px 0 0;
    height: 0;
    margin: 0 0 40px;
}

/* Text Elements */
p {
    margin-bottom: 15px;
}

strong {
    font-weight: bold;
}

cite,
em,
i {
    font-style: italic;
}

pre {
    background-color: #f1f1f1;
    margin-bottom: 20px;
    overflow: auto;
    padding: 30px;
}

pre,
code,
kbd {
    font-family: "Courier 10 Pitch", Courier, monospace;
}

abbr,
acronym,
dfn {
    border-bottom: 1px dotted rgba(0, 0, 0, 0.4);
    cursor: help;
}

address {
    display: block;
    margin: 0 0 1.625em;
}

ins {
    background-color: #fff9c0;
    text-decoration: none;
    margin-bottom: 20px;
}

/* Superscript and Subscript */
sup,
sub {
    font-size: 10px;
    height: 0;
    line-height: 1;
    position: relative;
    vertical-align: baseline;
}

sup {
    bottom: 1ex;
}

sub {
    top: .5ex;
}

/* Blockquote */
blockquote {
    font-style: italic;
    margin-bottom: 20px;
    background-color: #f1f1f1;
    padding: 30px 30px 15px 26px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    border-left: 4px solid #63c6ae;
}

blockquote em,
blockquote i,
blockquote cite {
    font-style: normal;
}

blockquote cite {
    letter-spacing: 2px;
    text-transform: uppercase;
}

/* Breadcrumb */
.breadcrumb {
    float: right;
    text-align: right;
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 12px;
}

.breadcrumb,
.breadcrumb a,
.breadcrumb a:hover {
    color: #fff;
    opacity: 0.7;
    -moz-opacity: 0.7;
    filter: alpha(opacity=70);
}

.breadcrumb,
.breadcrumb a:hover {
    opacity: 10;
    -moz-opacity: 10;
    filter: alpha(opacity=100);
}

/* Forms */
input[type="text"],
input[type="email"],
input[type="search"],
input[type="password"],
input[type="tel"],
input[type="url"],
input[type="date"],
textarea,
input[type="reset"],
input[type="button"],
input[type="submit"] {
    -moz-appearance: none !important;
    -webkit-appearance: none !important;
    appearance: none !important;
    -moz-box-sizing: inherit;
    -webkit-box-sizing: inherit;
    box-sizing: inherit;
}

input[type="text"],
input[type="email"],
input[type="search"],
input[type="password"],
input[type="tel"],
input[type="url"],
input[type="date"],
textarea {
    padding: 10px;
    border: 1px solid rgba(0, 0, 0, 0.05);
    width: 100%;
    margin: 0 0 30px;
    background-color: #f9f9f9;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="search"]:focus,
input[type="password"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
input[type="date"]:focus,
textarea:focus {
    border-color: rgba(0, 0, 0, 0.15);
}

input[type="reset"],
input[type="button"],
input[type="submit"] {
    border: 0 none;
    cursor: pointer;
    margin-bottom: 30px;
    color: #fff;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    display: block;
    text-align: center;
    height: 40px;
    padding: 0px 20px;
    margin-top: 5px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    background-color: #63c6ae;
    -ms-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

input[type="reset"]:hover,
input[type="button"]:hover,
input[type="submit"]:hover {
    background-color: #777;
}

input.s {
    height: 22px;
    margin: 0;
    width: 130px;
    padding: 6px 8px;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
}

select {
    padding: 10px;
    border: 1px solid rgba(0, 0, 0, 0.05);
    margin: 0 0 30px;
    width: 50%;
    background-color: #f9f9f9;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
}

input[type="radio"],
input[type="checkbox"],
input[type="file"] {
    margin-bottom: 30px;
}

input[type="file"] {
    border: 0 none;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    display: block;
    text-align: center;
    height: 40px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    -ms-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

/* Links */
a {
    color: #63c6ae;
    text-decoration: none;
}

a:focus,
a:active,
a:hover {
    text-decoration: none;
}

/* Images */
figure {
    margin: 0;
    text-align: center;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: top;
}

.entry-content img,
.comment-content img,
.widget img {
    max-width: 100%;
}

img.size-full,
img.size-large {
    max-width: 100%;
    height: auto;
}

.entry-content img.wp-smiley {
    border: none;
    margin-bottom: 0;
    margin-top: 0;
    padding: 0;
}

img.alignleft,
img.alignright,
img.aligncenter {
    margin-top: 20px;
    margin-bottom: 20px;
}

img#wpstats {
    display: none;
}

/* Assistive text */
.assistive-text {
    position: absolute !important;
    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
    clip: rect(1px, 1px, 1px, 1px);
}

/* Clearfix Hack */
.clearfix:after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
}

.clearfix {
    display: inline-block;
}

/* start commented backslash hack \*/
* html .clearfix {
    height: 1%;
}

.clearfix {
    display: block;
}

/* close commented backslash hack */
.clearfix-third,
.clearfix-fourth {
    clear: both;
}

/* Readmore */
a.readmore {
    color: #fff;
    display: inline-block;
    padding: 5px 25px 0;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    padding: 1px 8px 2px;
    margin-bottom: 7px;
    float: right;
    background-color: #63C6AE;
    -ms-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

a.readmore:hover {
    background-color: #777;
}

a.more-link:hover {
    text-decoration: underline;
}

/* Call To Action Button*/
.call-to-action {
    color: #fff;
    font-size: 14px;
    line-height: 21px;
    font-weight: bold;
    text-transform: uppercase;
    display: inline-block;
    text-align: center;
    padding: 9px 20px 9px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    background-color: #63c6ae;
    -ms-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.call-to-action:hover {
    background-color: #777;
}

/* Alignment */
.alignleft {
    display: inline;
    float: left;
    margin-right: 20px;
}

.alignright {
    display: inline;
    float: right;
    margin-left: 20px;
}

.aligncenter {
    clear: both;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

/* Back To Top */
.back-to-top {
    position: fixed;
    bottom: 30px;
    right: 30px;
}

.back-to-top a {
    width: 40px;
    height: 40px;
    display: block;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 16px;
    line-height: 40px;
    font-family: 'Genericons';
    background-color: #555;
    text-align: center;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    color: #fff;
    opacity: 0.8;
    -moz-opacity: 0.8;
    filter: alpha(opacity=80);
    -ms-transition: all 0.5s ease-out;
    -moz-transition: all 0.5s ease-out;
    -webkit-transition: all 0.5s ease-out;
    -o-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out;
}

.back-to-top a:hover {
    opacity: 10;
    -moz-opacity: 10;
    filter: alpha(opacity=100);
    background-color: #63C6AE;
}

.back-to-top a:before {
    content: '\f432';
}

/* =Header
----------------------------------------------- */

/* Header Info Bar */
.info-bar {
    background-color: #555;
    color: #999;
    padding-bottom: 6px;
}

.info-bar .info {
    float: left;
    padding: 7px 0 3px;
}

.info-bar .info ul li {
    float: left;
    margin-right: 20px;
}

.info-bar .info ul li:before {
    margin-top: 3px;
    margin-right: 5px;
    color: #63C6AE;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: inline-block;
    width: 16px;
    height: 16px;
    font-size: 16px;
    line-height: 16px;
    font-family: 'Genericons';
    vertical-align: top;
}

.info-bar .info ul li.phone-number:before {
    content: '\f437';
}

.info-bar .info ul li.email:before {
    content: '\f410';
}

.info-bar .info ul li.address:before {
    content: '\f417';
}

.info-bar .info ul li a {
    color: #999;
}

.info-bar .info ul li a:hover {
    text-decoration: underline;
}

/* Hgroup Wrap */
.hgroup-wrap {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

/* Logo, Title and Description */
#site-logo {
    float: left;
    margin-top: 32px;
}

#site-title {
    font-size: 28px;
    text-transform: uppercase;
    font-weight: normal;
    line-height: 35px;
}

#site-title a {
    color: #777;
    display: block;
}

#site-title a img {
    vertical-align: inherit;
    max-width: 978px;
    display: block;
}

#site-title a span,
#site-title a:hover,
#site-title a:focus,
#site-title a:active {
    color: #63C6AE;
}

#site-description {
    font-size: 12px;
    line-height: 12px;
    color: #999;
}

.hgroup-right {
    float: right;
    position: relative;
    padding-right: 35px;
}

