    font-weight: 500;
    padding: 5px 0 3px 0;
    text-transform: uppercase;
}

.widget_calendar #wp-calendar th {
    background-color: #f4f4f4;
    font-weight: bold;
}

.widget_calendar #wp-calendar tfoot td {
