    padding: 10px 0;
}

.widget_tag_cloud a:hover {
    color: #63C6AE;
}

/* Custom Tag Cloud Widget */
.widget_custom-tagcloud {
    margin-bottom: 42px;
}

.widget_custom-tagcloud a {
    margin: 0 4px 8px 0;
    background-color: #F9F9F9;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    display: inline-block;
    padding: 1px 8px 2px;
    color: #777;
