    max-width: 180px;
    margin-bottom: 15px;
    display: inline-block;
}

.widget_recent_work .custom-gallery-title a {
    margin-bottom: 5px;
}

.widget_recent_work .custom-gallery-title,
.widget_recent_work .custom-gallery-title a {
    text-align: left;
}

/* Our Clients Widget */
.widget_ourclients {
    margin-bottom: 0;
    padding: 20px 0;
    background-color: #f1f1f1;
}

.widget_ourclients ul {
    margin: 0;
    text-align: center;
}

.widget_ourclients ul li {
    margin: 0 10px;
    display: inline-block;
    max-width: 200px;
}

.widget_ourclients ul li a {
    display: block;
    opacity: 0.8;
    -moz-opacity: 0.8;
    filter: alpha(opacity=80);
}

.widget_ourclients ul li a:hover {
    opacity: 10;
    -moz-opacity: 10;
    filter: alpha(opacity=100);
}

/* Promotional Bar Widget */
.widget_promotional_bar {
    background-color: #f1f1f1;
    padding: 30px 30px 30px 26px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    border-left: 4px solid #63c6ae;
}

.widget_promotional_bar .promotional-text {
    font-size: 17px;
    line-height: 24px;
    font-weight: bold;
    text-transform: uppercase;
    color: #333;
    width: 80%;
    float: left;
}

.widget_promotional_bar .promotional-text span {
    font-size: 13px;
    line-height: 20px;
    font-weight: normal;
    text-transform: none;
    display: block;
    padding-top: 6px;
    color: #777;
}

.widget_promotional_bar .call-to-action {
    float: right;
    padding-left: 25px;
    padding-right: 25px;
    margin-top: 6px;
}

/* WP Caption */
.wp-caption {
    margin-bottom: 20px;
    max-width: 100%;
}

.wp-caption a {
    display: block;
    position: relative;
}

.wp-caption .wp-caption-text,
